.ideas-cards__list {
  display: grid;
  grid-template-columns: 2fr clamp(157px, 12vw, 235px);
  grid-template-columns: repeat(2, 1fr);
  column-gap: 4px;
  row-gap: 30px;
  margin-top: clamp(60px, 5vw, 80px);

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.ideas-cards__item {
  width: 100%;
  text-align: center;
}

.ideas-cards__item a {
  color: var(--back-main);
  text-decoration: none;
}

.ideas-cards__text {
  position: relative;
  font-family: inherit;
  font-size: clamp(25px, 2vw, 40px);
  padding-left: clamp(15px, 4vw, 13vw);
  padding-right: clamp(15px, 4vw, 13vw);
  margin: 0;
  margin-top: 2.3vw;

  @media (max-width: 767px) {
    font-size: 23px;
  }

  @media (max-width: 680px) {
    font-size: 20px;
  }
}

.ideas-cards__underline {
  background-image: linear-gradient(var(--basic-blue) 60%, transparent 50%);
  background-size: 100% 10px;
  background-position: 0 120%;
  background-repeat: no-repeat;
}
