.catalog-choice {
  display: grid;
  grid-template-columns: clamp(240px, 20vw, 390px) 1fr;
  column-gap: 2.4vw;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

.catalog-choice__sidenav {
  display: grid;
  grid-template-rows: repeat(2, max-content);
  margin-bottom: 30px;

  @media (max-width: 992px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
}

.catalog-choice .promotion {
  position: relative;
}

.catalog-choice .promotion img {
  display: grid;
  margin: 0 auto;
  outline: 1px solid blue;
}
