.property-goods {
  position: relative;
}

p.property-goods__text {
  padding: 0;
}

.property-goods__text p:last-child {
  display: flex;
}


.property-goods select {
  display: inline-block;
  width: 187px;
  height: 33px;
  font-family: inherit;
  font-size: clamp(20px, 1.4vw, 25px);
  margin-left: 20px;
  border: 1px solid var(--back-main);
  padding-left: 10px;
}

.property-goods option {

}

.property-goods option:checked {
  background: olive linear-gradient(olive, olive);
  color: blue;
}

.property-goods__content p {
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  padding: 0;
  margin: 0;
  margin-bottom: 2.6vw;
}

.property-goods__content a {
  font-size: clamp(28px, 2vw, 42px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  text-decoration: none;
  border-bottom: 4px solid var(--basic-green);
}

.property-goods__samples {
  display: flex;
  margin-top: 23px;
  margin-bottom: 35px;
}

.property-goods__samples li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 53px;
  height: 53px;
  margin-right: 7px;
  cursor: pointer;
}

.property-goods__samples span {
  display: block;
  width: 42px;
  height: 42px;
}

.property-goods__samples li:nth-child(1) span {
  background-color: var(--back-main);
}

.property-goods__samples li:nth-child(2) span {
  background-color: transparent;
  border: 1px solid #adadad;
}

.property-goods__samples li:nth-child(3) span {
  background-color: #dfccc0;
}

.property-goods__samples li:hover {
  border: 1px solid var(--basic-dark-pink);
}

.property-goods .promotion {
  text-align: center;
  margin-top: -30px;
}

.property-goods .promotion > p {
  position: absolute;
    width: 100%;
    bottom: 55%;
    left: 50%;
    transform: translateX(-50%);
    font-family: inherit;
    font-size: clamp(20px, 1.5vw, 30px);
    font-variation-settings: "wght" 400, "wdth" 100;
    color: var(--back-main);
    text-align: center;
    margin: 0;
    z-index: 20;
    padding: 0;
  }

.property-goods__content .info-card {
  position: relative;
  display: flex;
  padding-left: 1.8vw;
  margin-top: 2.7vw;
  z-index: 10;
}

.property-goods__content .info-card p {
  font-size: clamp(22px, 1.75vw, 33px);
  line-height: 48px;
  text-align: left;
  margin: 0;
}

.property-goods__content .info-card span {
  font-size: clamp(28px, 3vw, 50px);
  text-align: left;
}

.property-goods__content button {
  margin-left: 4.17vw;
}
